.SearchField path {
  fill: var(--icon_secondary);
}

.SearchField input {
  padding: 12px 42px;
  color: var(--accent);
}

.SearchField .Select__container {
  padding-left: 42px;
  color: var(--accent);
}

.SearchField .Input--ios .Input__border {
  background: var(--field_background_custom);
}

.SearchField .Select--ios .Select__container {
  background: var(--field_background_custom);
}

.SearchField .IconContainer {
  position: absolute;
  z-index: 3;
}

.SearchField .IconRight {
  top: 10px;
  right: 10px;
  color: var(--text_secondary);
}

.SearchField .IconLeft {
  left: 16px;
  top: 14px;
}

@keyframes focus-android {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

.SearchField:active .Input--android .Input__border::before {
  animation-name: focus-android;
  animation-duration: 0.14s;
  animation-fill-mode: forwards;
  animation-direction: normal;
}

.SearchField:active .Input--ios .Input__border {
  border-color: var(--accent);
}
